.container {
  max-width: 1760px;
  margin: auto;
  padding: 0 15px;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.block-link {
  display: block;
}

.custom-select {
  display: inline-block;
  outline: 0;
  border: none;
  background: url(../img/custom-select-arr.png) right center no-repeat;
  padding-right: 10px;
  text-decoration: none;
}

.custom-select__title {
  font: 400 13px "Open Sans", sans-serif;
  color: #fec458;
  border-bottom: 1px dashed #fec458;
  padding-bottom: 4px;
  display: block;
}

.custom-select__title:hover {
  color: #eaa321;
}

.btn {
  height: 35px;
  border: 1px solid #fec24a;
  border-radius: 18px;
  padding: 0 22px;
  font: 400 13px/35px "Open Sans", sans-serif;
  color: #fec24a;
  text-transform: uppercase;
  cursor: pointer;
  transition: all .3s ease-in-out;
  background: transparent;
  margin: 30px;
  display: inline-block;
  text-decoration: none;
  outline: 0;
}

.btn:hover {
  background: #fec24a;
  color: #000;
}

.default-link {
  transition: color .2s ease-in-out;
}

.default-link:hover {
  color: #fec24a;
}

.center-align {
  text-align: center;
}

body {
  font-family: "Open Sans", sans-serif;
}

/* =Sticky Footer
-------------------------------------------------------------- */

html,
body {
  height: 100%;
}

.all-wrapper {
  min-height: 100%;
  margin-bottom: -150px;
}

.all-wrapper:after {
  content: "";
  display: block;
}

.site-footer,
.all-wrapper:after {
  height: 150px;
}

/* =WordPress Core
-------------------------------------------------------------- */

.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  /* Image does not overflow the content area */
  padding: 5px 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

/* Text meant only for screen readers. */

.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  white-space: nowrap;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/*
The provided mixins are intended to be used with the array-like variables

.icon-home {
