.site-header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding: 30px 0;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  z-index: 999;
}

.site-header .container {
  position: relative;
}

.site-header__lside {
  float: left;
  width: 250px;
}

.site-header__tel {
  font: 500 21px "GothamPro", sans-serif;
  color: #fff;
  text-decoration: none;
}

.site-header__loc {
  font: 400 13px "Open Sans", sans-serif;
  margin: 8px 0 0;
}

.site-header__loc-anchor {
  margin-left: 5px;
}

.site-header__rside {
  width: 250px;
  float: right;
  padding-right: 45px;
  text-align: right;
}

.site-header__cart {
  padding-left: 25px;
  background: url(../img/header-cart-ico.png) left center no-repeat;
  text-decoration: none;
  color: #fff;
  font: 500 21px "GothamPro", sans-serif;
}

.site-header__cart-amount {
  margin-left: 8px;
}

.site-header__acc {
  font: 400 13px "Open Sans", sans-serif;
  margin-top: 8px;
}

.site-header__acc-link {
  color: #fff;
}

.site-header__acc-sep {
  margin: 0 5px;
}

.site-header__nav {
  margin: 0 270px;
}

.site-header__nav-list {
  margin: 16px 0 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.site-header__nav-list-item {
  display: inline-block;
  vertical-align: middle;
  padding: 0 25px;
  border-right: 1px solid rgba(255, 255, 255, 0.3);
}

.site-header__nav-list-item a {
  padding-left: 20px;
  color: #fff;
  text-transform: uppercase;
  font: 400 15px "Open Sans", sans-serif;
  text-decoration: none;
  background-repeat: no-repeat;
  background-position: left center;
}

.site-header__nav-list-item--large a {
  padding-left: 30px;
}

.site-header__nav-list-item:last-child {
  border: none;
}

.open-menu {
  border: none;
  background: transparent;
  outline: 0;
  cursor: pointer;
  position: absolute;
  width: 20px;
  height: 20px;
  padding: 0;
  top: 2px;
  right: 15px;
}

.open-menu span {
  display: block;
  height: 2px;
  background: #fff;
  margin-bottom: 4px;
}

.open-menu span:last-child {
  margin-bottom: 0;
}

.open-menu:hover span {
  background: #fec24a;
}

.home-top {
  height: 100vh;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  text-align: center;
  padding-top: 114px;
}

.home-top__content {
  max-width: 670px;
  margin: -50px auto 0;
}

.home-top__content-title {
  font: 300 55px "GothamPro", sans-serif;
  color: #fec458;
  letter-spacing: 0.025em;
  margin: 0 0 25px;
  padding-bottom: 25px;
  background: url(../img/header-border.png) bottom center no-repeat;
}

.home-top__content-description {
  font: 400 14px/22px "Open Sans", sans-serif;
  color: #fff;
  margin: 0 0 25px;
}

.home-top__form {
  position: relative;
  max-width: 370px;
  margin: 50px auto 0;
}

.home-top__form-search {
  height: 40px;
  display: block;
  width: 100%;
  background: transparent;
  color: #fff;
  font: 400 14px "Open Sans", sans-serif;
  outline: 0;
  border: none;
  border-bottom: 1px solid #fff;
  padding-right: 25px;
}

::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
}

:-moz-placeholder {
  color: rgba(255, 255, 255, 0.7);
}

::-moz-placeholder {
  color: rgba(255, 255, 255, 0.7);
}

:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.home-top__form-search:focus {
  border-color: #fec24a;
}

.home-top__form-submit {
  font-size: 0px;
  text-indent: -9999px;
  width: 15px;
  height: 15px;
  background: url(../img/submit-ico.png) no-repeat;
  outline: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 11px;
}

.big-banner {
  margin: 10px 0;
}

.big-menu {
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.big-menu__item {
  position: relative;
  height: 420px;
  overflow: hidden;
  width: calc(50% - 5px);
  margin-bottom: 10px;
  -webkit-backface-visibility: hidden;
  display: flex;
  align-items: center;
}

.big-menu__item:hover .big-menu__img {
  transform: rotate(0) scale(1.02);
}

.big-menu__content {
  background: rgba(0, 0, 0, 0.65);
  position: absolute;
  bottom: 40px;
  left: 40px;
  padding: 25px 40px;
  width: 310px;
}

.big-menu__content-logo {
  margin-bottom: 10px;
}

.big-menu__content-description {
  font: 300 36px "GothamPro", sans-serif;
  color: #fff;
  text-transform: uppercase;
  margin: 0;
}

.big-menu__content-description span {
  font-weight: 500;
  color: #fec458;
}

.big-menu__img {
  transform: rotate(3deg) scale(1.15);
  transition: 1s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  min-width: 50%;
  min-height: 50%;
  overflow-x: hidden;
}

.big-menu__form {
  position: relative;
  max-width: 700px;
  text-align: center;
  color: #fff;
  margin: auto;
}

.big-menu__form-title {
  font: 300 36px "GothamPro", sans-serif;
  margin: 0 0 30px;
  text-transform: uppercase;
}

.big-menu__form-row {
  display: flex;
  justify-content: space-between;
}

.big-menu__form-input {
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  display: block;
  border: none;
  outline: 0;
  padding: 0 12px;
  font: 300 14px "Open Sans", sans-serif;
  color: #fff;
  width: calc(33.33% - 8px);
  margin-bottom: 15px;
}

.big-menu__form-textarea {
  resize: none;
  height: 110px;
  width: 100%;
  padding: 12px;
  margin-bottom: 0;
}

.main-sidebar {
  background: #262626;
  width: 350px;
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
  z-index: 10;
  padding: 50px 0;
  transform: translateX(350px);
  transition: all .3s ease-in-out;
}

.main-sidebar.visible {
  transform: none;
}

.main-sidebar__close {
  position: absolute;
  background: url(../img/sidebar-close.png);
  width: 20px;
  height: 20px;
  top: 30px;
  right: 30px;
  padding: 0;
  cursor: pointer;
  border: none;
  outline: 0;
}

.main-sidebar__close:hover {
  opacity: .6;
}

.main-sidebar__block {
  padding: 20px 55px;
  border-top: 1px solid #3c3c3c;
  border-bottom: 1px solid #1a1a1a;
}

.main-sidebar__block:first-child {
  border-top: none;
}

.main-sidebar__block:last-child {
  border-bottom: none;
}

.main-sidebar__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-sidebar__list li {
  margin-bottom: 15px;
}

.main-sidebar__list li:last-child {
  margin-bottom: 0;
}

.main-sidebar__list a {
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  font: 400 15px "Open Sans", sans-serif;
}

.main-sidebar__list--gray a {
  color: #b2b2b2;
}

.site-footer {
  background: #0d0d0d;
  color: #fff;
}

.site-footer__top {
  padding: 22px 0;
  border-bottom: 1px solid #000;
}

.site-footer__lside {
  float: left;
}

.site-footer__lside p {
  margin: 0;
  text-transform: uppercase;
  font: 400 13px "Open Sans", sans-serif;
}

.site-footer__lside span {
  margin-bottom: 5px;
  display: block;
}

.site-footer__lside span:last-child {
  margin-bottom: 0;
}

.site-footer__lside-title {
  font-weight: 600;
}

.site-footer__rside {
  float: right;
}

.site-footer__bottom {
  border-top: 1px solid #1e1e1e;
